import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { RpJsonTitleDisplay, RpJsonToolbar } from "@rp/react/web/rpjson"

const DocumentViewerHeaderContainer = styled("div", { name: "ContentStyled" })(() => ({
  "& hr": {
    border: "none",
  },
}))

const DocumentViewerTitleContainer = styled(Box)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    background: theme.palette.surface.main,
    display: "flex",
    alignItems: "flex-start",
    flexShrink: 0,
    flexDirection: "column",
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(0.5),
    paddingRight: isMobile ? theme.spacing(2) : theme.spacing(4),
  }
})

const commonDocumentTitleStyles = {
  fontSize: 26,
  fontWeight: "bold",
  padding: 0,
  margin: 0,
  marginBottom: "12px",
}

const DocumentTitleSmall = styled(RpJsonTitleDisplay)({
  "& p": {
    ...commonDocumentTitleStyles,
    wordBreak: "break-word",
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
})

const DocumentTitleLarge = styled(RpJsonTitleDisplay)({
  "& p": {
    ...commonDocumentTitleStyles,
  },
})

const DocumentViewerSubheader = styled("div")(({ theme }) => ({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  width: "100%",

  "& > div": {
    marginBlock: theme.spacing(1.5),
  },
}))

const RpJsonToolbarStyled = styled(RpJsonToolbar, {
  shouldForwardProp: prop => prop !== "shouldDisplayControls",
})<{ customBackgroundColor?: string }>(({ customBackgroundColor, theme }) => ({
  "&": {
    backgroundColor: customBackgroundColor ?? theme.palette.surface.main,
    paddingBottom: theme.spacing(1.5),
    paddingRight: theme.spacing(4),
    width: "100%",
  },
  "& .MuiFormControl-root": {
    background: theme.palette.grey[700],
    "& fieldset": {
      borderColor: "transparent",
    },
    "&:hover fieldset": {
      borderColor: theme.palette.grey[300],
      cursor: "pointer",
    },
    "& .Mui-focused fieldset, &:hover .Mui-focused fieldset": {
      borderColor: theme.palette.primary.main,
      cursor: "pointer",
      borderWidth: "1px",
    },
  },
  "& button": {
    background: theme.palette.grey[900],
    color: theme.palette.grey[100],
    borderRadius: "4px",
  },
  "& button svg, & .MuiCheckbox-root svg": {
    width: "16px",
  },
}))

export {
  DocumentViewerHeaderContainer,
  DocumentViewerTitleContainer,
  DocumentTitleSmall,
  DocumentTitleLarge,
  DocumentViewerSubheader,
  RpJsonToolbarStyled,
}
